<template>
  <div id="TheComponentTable">
    <p class="left">於 components/ThePersonalTools/ 資料夾內</p>
    <table class="highlight">
      <thead>
        <tr>
          <td>組件名</td>
          <td>props</td>
          <td>元素</td>
          <td>預設 / 元素 / 使用方式</td>
        </tr>
      </thead>
      <tbody>
        <!-- <td>&lt;&gt;&lt;/&gt;</td> -->
        <tr>
          <td colspan="4">img</td>
        </tr>
        <tr>
          <td>VCircularImg<br>圓形圖片</td>
          <td>&lt;VCircularImg :src="" alt=""&gt;&lt;/VCircularImg&gt;</td>
          <td>:src (圖片位置)<br>alt (圖片名稱)</td>
          <td>:src (default:require('@/assets/img/headPhoto.png'))<br>
            img(:src="src" :alt="alt" class="circle responsive-img")<br>
            alt (default:Vue Image)<br><br>
            div(class="VCircularImg")<br>
            &nbsp;&nbsp;img(:src="src" :alt="alt" class="circle responsive-img")<br><br>
            - import Img form "@/assets/img/logo.png"<br>
            - data(){return{ img:Img }}<br>
            - 組件名上使用 :src="img"
          </td>
        </tr>
        <tr>
          <td colspan="4">btn</td>
        </tr>
        <tr>
          <td>VRaisedBtn<br>圖示按鈕</td>
          <td>&lt;VRaisedBtn name="" href="" icon="" vclass="" traget="" iclass=""&gt;&lt;/VRaisedBtn&gt;</td>
          <td>name (按鈕名稱)<br>
            href (連結)<br>
            icon (標誌名稱)<br>
            traget (連結視窗)<br>
            vclass (按鈕樣式)<br>
            iclass (標誌樣式)
          </td>
          <td>name (default:"按鈕")<br>
            href (default:"#!")<br>
            icon (default:"cloud")<br>
            traget (default:"")<br>
            vclass (default:"waves-effect waves-light")<br>
            iclass (default:"left")<br><br>
            div(class="VRaisedBtn")<br>
            a(:href="href" class="btn" :class="vclass" :traget="traget") {{name}}<br>
            i(class="material-icons" :class="iclass") {{icon}}
          </td>
        </tr>
        <tr>
          <td>VRaisedRouterLinkBtn<br>router-link 按鈕</td>
          <td>&lt;VRaisedRouterLinkBtn name="" to="" icon="" vcalss="" iclass=""&gt;
          &lt;/VRaisedRouterLinkBtn&gt;</td>
          <td>name (按鈕名稱)<br>
            to (Router-link)<br>
            icon (標誌名稱)<br>
            vclass (按鈕樣式)<br>
            iclass (標誌樣式)</td>
          <td>name (default:"按鈕")<br>
            to (default:"/")<br>
            icon (default:"cloud")<br>
            vclass (default:"waves-effect waves-light")<br>
            iclass (default:"left")<br><br>
            div(class="VRaisedRouterLinkBtn")<br>
            router-link(:to="to" class="btn" :class="vclass") {{name}}<br>
            i(class="material-icons" :class="iclass") {{icon}} 
          </td>
        </tr> 
        <tr>
          <td>VFloatingBtn<br>浮動按鈕</td>
          <td>&lt;VFloatingBtn href="" icon="" vclass=""&gt;&lt;/VFloatingBtn&gt;</td>
          <td>href (連結)<br>
              icon (標誌名稱)<br>
              vclass (按鈕樣式)<br>
              iclass (標誌樣式)
          </td>
          <td>
            href (default:"#!")<br>
            icon (default:"mode_edit")<br>
            vclass (default:"waves-effect waves-light")<br> <br>
            div(class="VFloatingBtn fixed-action-btn")<br>
            a(href="#!" class="btn-floating" :class="vclass") {{icon}}<br>
            i(class="material-icons" :class="iclass")
          </td>
        </tr>
        <tr>
          <td>VFlatBtn<br>無框按鈕</td>
          <td>&lt;VFlatBtn &gt;&lt;/VFlatBtn&gt;</td>
          <td>name (按鈕名稱)       
              href (連結)         
              icon(標誌名稱)            
              vclass(按鈕樣式)      
              iclass(標誌樣式)
          </td>
           <td>
            name(default:"按鈕")<br>        
            href (default:"#!")<br>         
            icon(default:"cloud")<br>           
            vclass(default:"")<br>      
            iclass(default:"left")<br><br>  
        
            div(class="VFlatBtn")<br>
            a(href="#!" class="btn-flat" :class="vclass") {{name}}<br>
            i(class="material-icons" :class="iclass") {{icon}}
          </td>
        </tr>
        <tr>
          <td colspan="4">layout</td>
        </tr>
         <tr>
          <td colspan="4">other</td>
        </tr>
        <tr>
          <td>VIcon<br>標誌</td>
          <td>&lt;VIcon icon="" iclass=""&gt;&lt;/VIcon&gt;</td>
          <td>icon (標誌名稱)<br>
            vclass (樣式)</td>
          <td>
            icon (default:"add_circle)<br>
            iclass (default:"pink-text lighten-5")<br><br>
            div(class="VIcon")<br>
            i(class="material-icons" :class="iclass") {{icon}}
          </td>
        </tr>
        <!-- 
        <tr>
          <td></td>
          <td>&lt;&gt;&lt;/&gt;</td>
          <td></td>
          <td></td>
        </tr> 
        -->
      </tbody>
    </table>
  </div>
</template>
<script>
export default {
  name: 'TheComponentTable',
  data() {
    return {
      icon: "{{icon}}",
      name: "{{name}}"
    };
  },
};
</script>
